
.terminals {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  align-items: center;
  color: white;

  .titleWrap {
    display: flex;
    flex-direction: column;
    .setting{
      display: flex;

    }
    .title {
      display: flex;
      justify-content: center;
      margin: 20px auto;
      font-size: 50px;
    }
  }

  .tableWrap {
    display: flex;

    .table {
      color: white;
      width: 70%;
      border-collapse: collapse;
      //border: white 1px solid;
      min-width: 280px;
    }
  }
}


